import React from "react"
import { useSpring, animated, config } from "react-spring"

const Info = () => {
    const [flip, set] = React.useState(false)
    const props = useSpring({
        to: { opacity: 1, transform: "translateX(0px)", color: "hotpink" },
        from: { opacity: 0, transform: "translateX(-200px)" },
        reverse: flip,
        delay: 1000,
        config: config.molasses,
    })

    return (
        <>
            <animated.h1 style={props}>阿瑟跟念念的囍宴終於可以舉辦了!!</animated.h1>
            <animated.h1 style={props}>這邊 先謝謝各位耐心的等待</animated.h1>
            <animated.h1 style={props}>大家 當天蒞臨時 可以直接停到對面的停車場</animated.h1>
            <animated.h1 style={props}>到時離場時 可以跟旁邊的工作人員索取停車券(可免費停車三小時)</animated.h1>
        </>
    )
}
export default Info
